import React, { Component } from 'react';
import './../../Styles/Styles.scss';
import './GhiNhoContent.scss'
import Aux from '../../../hoc/_Aux/_Aux';
import instanceAxios from '../../../axios-orders';

class GhiNhoContent extends Component {
  state = {
    mon: '',
    lop: '',
    chuong: '',
    bai: '',
    chuyende: false,
    congthuc: 'Công thức'
  }

  onChangeHandler = (event) => {
    this.setState({
      ...this.state,
      [event.target.name]: event.target.value
    })
  }

  switchChuyenDeHandler = (event) => {
    event.preventDefault();
    event.stopPropagation();
    console.log(event.target)
    this.setState({
      ...this.state,
      chuyende: !this.state.chuyende
    })
  }

  switchCongThucHandler = (event) => {
    event.preventDefault();
    // let ghinho = document.getElementById('ghinho');
    // let btns = ghinho.querySelectorAll('.btn');
    // console.log(btns)
    let text = this.state.congthuc === 'Công thức' ? 'Phương pháp' : 'Công thức';
    this.setState({
      ...this.state,
      congthuc: text
    })
  }

  onSubmitHandler = (event) => {
    event.preventDefault();
    this.props.dataHandler();

    instanceAxios.get('/api/method').then(res => {
      console.log(res.data.data)
    })
  }

  render() {
    let congthucStyle = this.state.congthuc === 'Công thức'? "btn-warning": null;

    let chuyende = <Aux>
      <div className="col-6">
        <div className="input-group input-group-sm">
          <div className="input-group-prepend">
            <button className={[congthucStyle, "btn btn-switch"].join(' ')} onClick={this.switchChuyenDeHandler}><i className="fa fa-refresh"></i> Lớp</button>
          </div>
          <select name="lop" id="lop" className="form-control">
            <option value="Lớp 10">Lớp 10</option>
            <option value="Lớp 11">Lớp 11</option>
            <option value="Lớp 12">Lớp 12</option>
          </select>
        </div>
      </div>

      <div className="col-6">
        <div className="input-group input-group-sm">
          <div className="input-group-prepend">
            <button className={[congthucStyle, "btn"].join(' ')} disabled>Chương</button>
          </div>
          <select name="chuong" id="chuong" className="form-control">
            <option value="Chương 1">Chương 1</option>
            <option value="Chương 2">Chương 2</option>
            <option value="Chương 3">Chương 3</option>
          </select>
        </div>
      </div>

      <div className="col-6">
        <div className="input-group input-group-sm">
          <div className="input-group-prepend">
            <button type="button" className={[congthucStyle, "btn"].join(' ')} disabled>bài</button>
          </div>
          <select name="bai" id="bai" className="form-control">
            <option value="Bài 1">Bài 1</option>
            <option value="Bài 2">Bài 2</option>
            <option value="Bài 3">Bài 3</option>
          </select>
        </div>
      </div>
    </Aux>

    if (this.state.chuyende === true) {
      chuyende = <Aux>
        <div className="col-6">
          <div className="input-group input-group-sm">
              <button className={[congthucStyle, "btn btn-sm btn-switch w-100"].join(' ')} onClick={this.switchChuyenDeHandler}><i className="fa fa-refresh"></i> Chuyên đề</button>
          </div>
        </div>
      </Aux>
    }

    return (
      <form className="Form" onSubmit={this.onSubmitHandler}>
        <div className="row">
          <div className="col-12">
            <div className="input-group input-group-sm mb-2">
              <input type="text" className="form-control" placeholder="Tìm kiếm ..."/>
              <div className="input-group-append">
                <button className="btn btn-danger" type="button">Tìm</button>
              </div>
            </div>
          </div>



          <div className="col-6">
            <div className="input-group input-group-sm">
              <div className="input-group-prepend">
                <button className={[congthucStyle, "btn btn-switch"].join(' ')} disabled>Môn</button>
              </div>
              <select name="mon" id="mon" className="form-control">
                <option value="Hóa">Hóa</option>
                <option value="Lý">Lý</option>
                <option value="Toán">Toán</option>
              </select>
            </div>
          </div>

          {chuyende}

          <div className="col-6">
            <button className="btn btn-danger btn-sm w-100">Enter</button>
          </div>

          <div className="col-6">
            <button className={[congthucStyle, "btn btn-sm btn-switch w-100"].join(' ')} onClick={this.switchCongThucHandler}>
              <i className="fa fa-refresh"></i> {this.state.congthuc}
            </button>
          </div>


        </div>


      </form>
    );
  }
}

export default GhiNhoContent;



